export default {
  name: 'privacy-page',
  data() {
    return {
      title: 'Privacy Policy Surat Online',
      datePrivacyPolicy: '',
    };
  },
  mounted() {
    this.initDates();
  },
  methods: {
    initDates() {
      const date = new Date();
      const stringTahun = date.getFullYear();
      const dateCopyright = `${stringTahun} `;
      this.datePrivacyPolicy = dateCopyright;
    },
    initBurger() {
      const burger = document.querySelector('.burger');
      const menu = document.querySelector(`#${burger.dataset.target}`);
      burger.classList.toggle('is-active');
      menu.classList.toggle('is-active');
    },
  },
};
